/* Dialog buttons */

.remodal-confirm,
.remodal-cancel {
    font: inherit;

    display: inline-block;
    overflow: visible;

    min-width: 110px;
    margin: 0;
    padding: 12px 0;

    cursor: pointer;
    transition: background 0.2s;
    text-align: center;
    vertical-align: middle;
    text-decoration: none;

    border: 0;
    outline: 0;
}

.remodal-confirm {
    color: #fff;
    background: #81c784;
}

.remodal-confirm:hover,
.remodal-confirm:focus {
    background: #66bb6a;
}

.remodal-cancel {
    color: #fff;
    background: #e57373;
}

.remodal-cancel:hover,
.remodal-cancel:focus {
    background: #ef5350;
}

/* Remove inner padding and border in Firefox 4+ for the button tag. */

.remodal-confirm::-moz-focus-inner,
.remodal-cancel::-moz-focus-inner,
.remodal-close::-moz-focus-inner {
    padding: 0;

    border: 0;
}

/* Keyframes
   ========================================================================== */

@keyframes remodal-opening-keyframes {
    from {
        transform: scale(1.05);

        opacity: 0;
    }

    to {
        transform: none;

        opacity: 1;

        filter: blur(0);
    }
}

@keyframes remodal-closing-keyframes {
    from {
        transform: scale(1);

        opacity: 1;
    }

    to {
        transform: scale(0.95);

        opacity: 0;

        filter: blur(0);
    }
}

@keyframes remodal-overlay-opening-keyframes {
    from {
        opacity: 0;
    }

    to {
        opacity: 1;
    }
}

@keyframes remodal-overlay-closing-keyframes {
    from {
        opacity: 1;
    }

    to {
        opacity: 0;
    }
}

/* Media queries
   ========================================================================== */

@media only screen and (min-width: 641px) {
    .remodal {
        max-width: 498px;
        max-height: auto;
    }
}

@media (min-width: 768px) {
    .remodal .ajax-form {
        padding: 18px 46px;
    }
}

.remodal-overlay {
    background: rgba(0, 0, 0, 0.76);
}

/* IE8
   ========================================================================== */

.lt-ie9 .remodal-overlay {
    background: #2b2e38;
}

.lt-ie9 .remodal {
    width: 700px;
}

.remodal .button {
    color: #fff;
}

form input[type="email"],
form textarea,
form select {
    border: 1px solid #D2DBE0;
    border-radius: 3px;
    -webkit-box-shadow: none;
    box-shadow: none;
    margin: 0;
    padding: 0 16px;
    font-size: 16px;
    width: 100%;
    display: block;
    color: #203752;
    font-family: "Montserrat", sans-serif;
}

form input[type="email"],
form select {
    height: 53px;
}

@media (min-width: 768px) {
    .remodal-close::before {
        width: 50px;
        height: 50px;
        top: 0;
        right: -50px;
    }
}